.vmodal {
  display: flex;
  align-items: center;
  justify-content: center; }
  .vmodal-btn {
    display: inline-block; }
  .vmodal .h2 {
    margin: 0 0 10px;
    padding: 0;
    font-family: "uni_neueheavy", "Helvetica Neue", "Helvetica Extra Bold", "Arial Extra Bold", sans-serif;
    font-size: 32px;
    font-size: 3.2rem;
    color: #202e39;
    font-weight: 900;
    line-height: 40px;
    letter-spacing: 0.25px; }
  .vmodal .line {
    margin: 11px auto;
    height: 4px;
    width: 98px;
    background-color: #0AA5AB; }
  .vmodal .vcontent {
    padding: 0 38px 100px 100px;
    width: 750px;
    max-height: 568px;
    background-color: #FFF;
    border: none;
    border-radius: 8px;
    box-shadow: 0 10px 60px -3px rgba(0, 0, 0, 0.1);
    box-sizing: border-box; }
    .vmodal .vcontent:focus {
      outline: none; }
  .vmodal .vhdr {
    height: 100px;
    box-sizing: border-box;
    text-align: right; }
    .vmodal .vhdr .vbtn-close {
      margin-top: 25px; }
  .vmodal .vscroll {
    padding: 0px;
    padding-right: 62px;
    max-height: 368px;
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden; }
  .vmodal .vbody {
    padding: 0px;
    text-align: center; }
  .vmodal .vftr {
    padding: 60px 0px 0px;
    text-align: center; }
