/*	Mixing key - vars
	1. Sets Font-Family 
		a) $SourceSansPro
		b) $uniNeue
		c) $uniNeue-Heavy 
	2. All the color in the color palette set in DSG 2.2 are avariable. The color variables are setup takeing the color name and add a $ infront; i.e. $gray700 = #32424e and $teal500 = #26a1a1

*/
/* Mixing key - Mixin's and functions

	1. font-size(1.6) : returns font-size: 16px; font-size: 1.6rem; font-size: 1em;
	2. fontStyle(family, size, weight, color, line-height) creates a full styled font. 
			The default values are($family:$SourceSansPro, $size:1.6, $weight: 400, $color:$gray700, $lh: 1.75)
			here are some examples how to use it 
					@include fontStyle();
					@include fontStyle(arial,1.4,$lh:1);
					@include fontStyle($size:2.2);
					@include fontStyle($weight:400,$color: $gray100);
*/
